*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  height: auto;
  max-width: 100%; }

body {
  min-width: 320px;
  font-family: "Styrene A Web", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19.2px;
  color: #262626; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 600px; }

@media (min-width: 900px) {
  body {
    color: #000000; }
  .container {
    padding: 0 77px;
    max-width: 1440px; } }

@keyframes progress {
  from {
    left: 0;
    transform: translateX(-100%); }
  to {
    left: 100%;
    transform: translateX(100%); } }

.main__stream {
  display: flex;
  flex-direction: column;
  padding: 28px 0 47px 0;
  min-height: 100vh; }

.main__stream-title {
  margin-top: 0;
  margin-bottom: 34px;
  font-size: 28px;
  line-height: 33.6px;
  font-weight: 500; }

.main__specialty {
  margin-bottom: 26px; }
  .main__specialty:last-of-type {
    margin-bottom: 44px; }

.main__stream-error {
  text-align: center;
  font-size: 18px;
  line-height: 24px;
  color: #000000; }

.main__stream-progress {
  position: relative;
  margin-bottom: 43px;
  height: 5px;
  overflow: hidden; }
  .main__stream-progress::before, .main__stream-progress::after {
    content: "";
    position: absolute;
    top: 0;
    height: 100%;
    width: 60%;
    border-radius: 100em;
    background-color: #ff3535;
    animation-name: progress;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-iteration-count: infinite; }
  .main__stream-progress::after {
    left: 0;
    transform: translateX(-100%);
    animation-delay: 1s; }

.main__specialty ~ .main__stream-progress {
  display: none; }

.main__stream-error ~ .main__stream-progress::before, .main__stream-error ~ .main__stream-progress::after {
  background-color: #d9d9d9; }

.main__supply-wrapper {
  margin-top: auto; }

.main__supply {
  margin-bottom: 23px;
  padding: 31px 22px 41px 22px;
  color: #ffffff;
  background-color: #ff3535; }
  .main__supply--background-dark {
    background-color: #2d2c2a; }
  .main__supply:last-child {
    margin-bottom: 0;
    padding-bottom: 62px; }

.main__supply-title {
  margin-top: 0;
  margin-bottom: 25px;
  font-size: 25px;
  line-height: 30px; }

.main__supply-text {
  font-size: 20px;
  line-height: 26px;
  font-weight: 300; }

.main__supply-list {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 26px;
  font-weight: 300;
  list-style: none; }

.main__supply-item {
  position: relative;
  margin-bottom: 15px;
  padding-left: 20px; }
  .main__supply-item:last-child {
    margin-bottom: 0; }
  .main__supply-item::before {
    content: "";
    position: absolute;
    top: 7px;
    left: 0;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #ff3535; }

@media (min-width: 900px) {
  .main__stream {
    padding-top: 71px;
    padding-bottom: 171px; }
  .main__stream-title {
    margin: 0 auto 89px auto;
    font-size: 36px;
    line-height: 43.2px;
    font-weight: 700; }
  .main__specialty {
    margin-bottom: 68px; }
    .main__specialty:last-of-type {
      margin-bottom: 131px; }
  .main__supply-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px; }
  .main__supply {
    margin-bottom: 0;
    padding: 36px; }
  .main__supply-title {
    font-size: 36px;
    line-height: 43.2px; } }

@media (min-width: 1200px) {
  .main__supply {
    margin-bottom: 0;
    padding: 56px; } }

.specialty__title {
  margin-top: 0;
  margin-bottom: 27px;
  font-size: 22px;
  line-height: 26.4px;
  font-weight: 700; }

.specialty__module {
  margin-bottom: 10px; }
  .specialty__module:last-child {
    margin-bottom: 0; }

@media (min-width: 900px) {
  .specialty__module {
    margin-bottom: 50px; }
    .specialty__module:last-child {
      margin-bottom: 0; } }

@media (min-width: 1200px) {
  .specialty {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 45px;
    row-gap: 45px; }
  .specialty__title {
    grid-column: 1 / -1;
    margin-bottom: 0;
    font-size: 26px;
    line-height: 31.2px; }
  .specialty__module {
    margin-bottom: 0; } }

.module__title {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 14px 37px 14px 69px;
  font-size: 18px;
  line-height: 23.4px;
  font-weight: 300;
  background-color: #f7f7f7;
  transition-property: background-color, color;
  transition-duration: 400ms; }
  .module__title::before, .module__title::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 24px;
    transform: translateY(-50%);
    width: 18px;
    height: 2px;
    background-color: #d9d9d9; }
  .module__title::after {
    transform: translateY(-50%) rotate(90deg) rotateY(0);
    transition-property: transform;
    transition-duration: 400ms; }
  .module__title--active {
    background-color: #ff3535;
    color: #ffffff; }
    .module__title--active::before, .module__title--active::after {
      background-color: #ffffff; }
    .module__title--active::after {
      transform: translateY(-50%) rotate(90deg) rotateY(90deg); }

.module__button {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none; }

.module__button:not(:focus-visible) {
  outline: none; }

.module__button:focus-visible {
  outline: auto; }

.module__list-wrapper {
  display: none;
  max-height: 0;
  overflow: hidden;
  transition-property: max-height;
  transition-duration: 400ms; }

.module__list {
  margin: 0;
  padding: 17px 0;
  font-size: 15px;
  line-height: 19.5px;
  font-weight: 300;
  list-style: none; }

.module__item {
  position: relative;
  margin-bottom: 10px;
  padding-left: 20px; }
  .module__item:last-child {
    margin-bottom: 0; }
  .module__item::before {
    content: "";
    position: absolute;
    top: 7px;
    left: 0;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #ff3535; }

@media (min-width: 900px) {
  .module {
    position: relative;
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 40px;
    padding-top: 30px; }
    .module::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 140px;
      height: 2px;
      background-color: #ff3535; }
  .module__title {
    padding: 0;
    font-size: 32px;
    line-height: 38.4px;
    font-weight: 400;
    background-color: transparent;
    transition: none; }
    .module__title::before, .module__title::after {
      display: none; }
    .module__title--active {
      background-color: transparent;
      color: #000000; }
  .module__button {
    display: none; }
  .module__list-wrapper {
    display: block;
    padding-top: 7px;
    max-height: 100%; }
  .module__list {
    padding: 0;
    font-size: 16px;
    line-height: 19.2px; } }

@media (min-width: 1200px) {
  .module {
    grid-gap: 80px; } }
